import Vue from 'vue'
/**
 * é˜²æŠ– å°±æ˜¯æŒ‡è§¦å‘äº‹ä»¶åŽåœ¨  ç‰¹å®šé—´éš”å†… å‡½æ•°åªèƒ½æ‰§è¡Œä¸€æ¬¡ï¼Œå¦‚æžœåœ¨æ—¶é—´é—´éš”å†…åˆè§¦å‘äº†äº‹ä»¶ï¼Œåˆ™ä¼šé‡æ–°è®¡ç®—å‡½æ•°æ‰§è¡Œæ—¶é—´
 *ä¸‰ä¸ªå…¥å‚ å“åº”å‡½æ•°å æ—¶é•¿ ç»‘å®šäº‹ä»¶å
 *  v-debounce="['addF',1000,'click']"  
 */
Vue.directive('debounce', {
  inserted: function (el, bindingObj) {
    let timer //å®šä¹‰è®¡æ—¶å™¨å¯¹è±¡
    let [fn, time = 300, attr = 'click'] = bindingObj.value //è§£æž„å–å€¼ æ—¶é—´é»˜è®¤300æ¯«ç§’ ç»‘å®šäº‹ä»¶é»˜è®¤click
    el.addEventListener(attr, () => {
      if (timer) {
        clearTimeout(timer)
      }
      timer = setTimeout(() => fn(), time)
    })
  }
})



