import Vue from 'vue'
/**
 * èŠ‚æµ  æŒç»­è§¦å‘æŸä¸ªäº‹ä»¶ ç‰¹å®šæ—¶é—´é—´éš”å†…åªæ‰§è¡Œä¸€æ¬¡
 *ä¸‰ä¸ªå…¥å‚ å“åº”å‡½æ•°å æ—¶é•¿ ç»‘å®šäº‹ä»¶å
 *  v-throttle="['addF',1000,'click']"  
 */
Vue.directive('throttle', {
  inserted: function (el, bindingObj) {
    let flag, timer
    let [fn, time = 300, attr = 'click'] = bindingObj.value //è§£æž„å–å€¼ æ—¶é—´é»˜è®¤300æ¯«ç§’ ç»‘å®šäº‹ä»¶é»˜è®¤click
    el.addEventListener(attr, () => {

      if (flag) {
        //è¿˜æ²¡æ¸…é™¤åˆè§¦å‘äº†   å°±ä¸­æ–­æ‰§è¡Œreturn false  é‡æ–°å€’è®¡æ—¶ 
        console.log('enter flag')
        clearTimeout(timer)
        timer = setTimeout(() => fn(), time)
        return false
      }
      fn()
      flag = setTimeout(() => flag = null, time) //è§¦å‘åŽ è§„å®šæ—¶é—´é—´éš” å°†flagæ¸…é™¤ 
    })
  }
})



