<template>
  <transition>
    <div
      ref="breathing_lamp"
      class="breathing_lamp"
      @click="onclick()"
      @touchstart.stop="handleTouchStart"
      @touchmove.prevent.stop="handleTouchMove($event)"
      @touchend.stop="handleTouchEnd"
      :style="{
        left: left + 'px',
        top: top + 'px',
        width: itemWidth + 'px',
        height: itemHeight + 'px'
      }"
      v-if="isShow"
    >
      <span class="fixBut"
        ><span class="inner">å‰©ä½™<br />50w</span></span
      >
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    // çƒåå­—é»˜è®¤ï¼šâ€œçƒâ€

    // çƒå®½åº¦é»˜è®¤ï¼šâ€œ40â€
    itemWidth: {
      type: Number,
      default: 40
    },
    // çƒé«˜åº¦é»˜è®¤ï¼šâ€œ40â€
    itemHeight: {
      type: Number,
      default: 40
    }
  },
  data() {
    return {
      left: 0, // è·ç¦»å·¦è¾¹è·ç¦»
      top: 0, // è·ç¦»æŠ¬å¤´è·ç¦»
      startToMove: false, // å¼€å§‹ç§»åŠ¨æ—¶å€™ä¸æ˜¾ç¤º
      isShow: true, // ç»„ä»¶æ˜¯å¦æ˜¾ç¤º
      timer: null, // å®šæ—¶å™¨
      currentTop: null, // èŽ·å–å½“å‰é¡µé¢çš„æ»šåŠ¨æ¡çºµåæ ‡ä½ç½®
      clientW: document.documentElement.clientWidth, //è§†å£å®½
      clientH: document.documentElement.clientHeight //è§†å£é«˜
    };
  },
  created() {
    // åˆå§‹åŒ–å®šä¹‰è·ç¦»å››å‘¨è·ç¦»
    this.left = this.clientW - this.itemWidth - 45;
    this.top = this.clientH / 2 - this.itemHeight / 2 + 75;
  },

  methods: {
    // ç‚¹å‡»å°çƒäº‹ä»¶
    onclick() {
      console.log("I am a small clouds");
    },

    // å¼€å§‹ç§»åŠ¨æ–¹æ³•
    handleTouchStart() {
      this.startToMove = true;
      this.$refs.breathing_lamp.style.transition = "none";
    },

    // ç§»åŠ¨ä¸­æ–¹æ³•
    handleTouchMove(e) {
      const clientX = e.targetTouches[0].clientX; //æ‰‹æŒ‡ç›¸å¯¹è§†å£çš„x
      const clientY = e.targetTouches[0].clientY; //æ‰‹æŒ‡ç›¸å¯¹è§†å£çš„y
      const isInScreen =
        clientX <= this.clientW &&
        clientX >= 0 &&
        clientY <= this.clientH &&
        clientY >= 0;
      if (this.startToMove && e.targetTouches.length === 1) {
        if (isInScreen) {
          this.left = clientX - this.itemWidth / 2;
          this.top = clientY - this.itemHeight / 2;
        }
      }
    },

    // ç§»åŠ¨ç»“æŸæ–¹æ³•
    handleTouchEnd() {
      if (this.left < this.clientW / 2) {
        this.left = 0; //ä¸è®©è´´è¾¹ æ‰€ä»¥è®¾ç½®30æ²¡è®¾ç½®0
        this.handleIconY();
      } else {
        this.left = this.clientW - this.itemWidth - 45; //è·è¾¹30px
        this.handleIconY();
      }
      this.$refs.breathing_lamp.style.transition = "all .3s";
    },

    // ä¸Šä¸‹ä¸è´´è¾¹æ–¹æ³•
    handleIconY() {
      if (this.top < 0) {
        this.top = 30; //ä¸ä¸Šå¸–ä¸Šè¾¹æ‰€ä»¥è®¾ç½®ä¸º30 æ²¡è®¾ç½®0
      } else if (this.top + this.itemHeight > this.clientH) {
        this.top = this.clientH - this.itemHeight - 30; //è·è¾¹30px
      }
    }
  }
};
</script>

<style lang="less" scoped>
.breathing_lamp {
  position: fixed;
  z-index: 99998;
    .fixBut {
        z-index: 99999;
    background: orange;
    color: white;
    width: 150px;
    height: 150px;
    border-radius: 150px;
    text-align: center;
    line-height: 150px;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    .inner {
      line-height: 40px;
    }
  }
}
</style>



