<template>
    <div id="app">
        <div class="buttonList flex">
            <input ref="input" type="file" @change="uploadF" multiple />
            <button @click="compress">åŽ‹ç¼©</button>
        </div>
        <!-- åŽ‹ç¼©å‰å›¾ç‰‡ -->
        <div v-if="previewList.length > 0" class="preview flex" ref="preview">
            <img ref="preImg" v-for="(item, index) in previewList" class="new" :key="index" :src="item.src" />
        </div>
        <!-- åŽ‹ç¼©åŽå›¾ç‰‡ -->
        <div v-if="finalList.length > 0" class="preview flex">
            <img ref="finalImg" v-for="(item, index) in finalList" class="new" :key="index" :src="item.src" />
        </div>
    </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
            msg: 'å›¾ç‰‡åŽ‹ç¼©',
            previewList: [],//æ¸²æŸ“é¢„è§ˆå›¾ç‰‡
            finalList: [],//åŽ‹ç¼©åŽå›¾ç‰‡
            maxW: 80,//æœ€å¤§å®½
            maxH: 80,//æœ€å¤§é«˜
        }
    },
    methods: {
        /**
         * ä¸Šä¼ å›¾ç‰‡ä¿¡æ¯
         */
        uploadF(e) {

            const imgArr = e.target.files
            if (imgArr.length) {
                [].forEach.call(imgArr, this.readAndPreview);
            }
        },

        /**
         * è¯»å–å’Œé¢„è§ˆå›¾ç‰‡
         */
        readAndPreview(file) {
            let vm = this
            console.log('file', file)
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.addEventListener("load", function () {
                vm.previewList.push({ file: file, src: this.result });
            }, false);
            //true æ˜¯æ•èŽ·  é»˜è®¤æ˜¯falseå†’æ³¡
        },

        /**
         * åŽ‹ç¼©
         */
        compress() {
            if (this.previewList.length == 0) {
                alert("è¯·é€‰æ‹©å›¾ç‰‡")
                return
            }
            [].forEach.call(this.$refs.preImg, this.compressF);
        },
        /**
         * åŽ‹ç¼©é€»è¾‘
         */
        compressF(img) {
            let vm = this
            let naturalWidth = img.naturalWidth
            let naturalHeight = img.naturalHeight
            let targetW = null
            let targetH = null
            // åˆ¤æ–­å›¾ç‰‡æ˜¯å¦éœ€è¦åŽ‹ç¼©
            if (naturalWidth > this.maxW || naturalHeight > this.maxH) {
                let scale = naturalWidth / naturalHeight;
                if (scale > (this.maxW / this.maxH)) {
                    //Math å¯¹è±¡å®žä¾‹ round() æ–¹æ³•å¯æŠŠä¸€ä¸ªæ•°å­—èˆå…¥ä¸ºæœ€æŽ¥è¿‘çš„æ•´æ•°
                    // æ›´å®½ æŒ‰æœ€å¤§å®½åº¦ ç­‰æ¯”ä¾‹è®¾ç½®å°ºå¯¸
                    targetW = this.maxW
                    targetH = Math.round(this.maxH * (naturalHeight / naturalWidth))
                } else {
                    // æ›´é«˜ï¼ŒæŒ‰æœ€å¤§é«˜åº¦ ç­‰æ¯”ä¾‹è®¾ç½®å°ºå¯¸
                    targetH = this.maxH
                    targetW = Math.round(this.maxH * (naturalWidth / naturalHeight))
                }

            } else {
                alert('æ— éœ€åŽ‹ç¼©')
                return false
            }

            let imgN = new Image()
            imgN.src = img['src'] // å›¾ç‰‡çš„åœ°å€
            imgN['width'] = targetW
            imgN['height'] = targetH
            imgN.onload = function () {
                let canvas = document.createElement('canvas')
                canvas.width = targetW; /*è®¾ç½®æ–°çš„å›¾ç‰‡çš„å®½åº¦*/
                canvas.height = targetH; /*è®¾ç½®æ–°çš„å›¾ç‰‡çš„é•¿åº¦*/
                let ctx = canvas.getContext('2d')
                ctx.clearRect(0, 0, targetW, targetH)
                ctx.drawImage(this, 0, 0, targetW, targetH)
                let dataURL = canvas.toDataURL("image/png", 1);
                vm.finalList.push({ src: dataURL });
                vm.$nextTick(() => {
                    console.log(vm.$refs.finalImg)
                })

                // blobæ ¼å¼ä¸Šä¼ 
                canvas.toBlob(blob => {
                    console.log(blob)
                    // åŽå°æŽ¥å£äº¤äº’
                })

            }

        }

    }
}
</script>

<style lang="less" scoped>
@import "../main.less";
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 24px;
    .buttonList {
        display: flex;
        width: 690px;
        justify-content: space-between;
        margin: 30px auto;
        input {
            display: inline-block;
        }

        button {
            width: 100px;
        }
    }

    .preview {
        margin: 30px auto;
        width: 80%;
        background: lightgray;
        padding: 30px;
        flex-wrap: wrap;

        img {
            margin: 10px;
            width: 100px;
            
        }
    }
}
</style>



