<template>
  <div class="bj">
    <div class="column">
      <span>demoBasic</span>
      vuexæ•°æ®{{ dataTest }}
      <van-button type="info" v-debounce="[addF,1000]">ç‚¹å‡»æµ‹è¯•debounce</van-button>
      <van-button type="info" v-throttle="[delF,1000]">ç‚¹å‡»æµ‹è¯•throttle</van-button>
      count{{ count }}
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import dayjs from "dayjs";

// import { dateTransfor } from "../utils";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  props: {
    // arr: {
    //   type: Array,
    //   default:()=>[]
    // },
  },
  components: {},
  data() {
    return {
      count: 0,
      currentDate: null,
    };
  },
  created() {},
  mounted() {},
  methods: {
    ...mapMutations({
      changeDataTest: "changeDataTest",
    }),
    addF() {
      this.count++;
    },
    delF(){
        this.count--; 
    }
  },
  watch: {
    //'list.xx':{} ç›‘å¬ä¸€ä¸ªå±žæ€§çš„å†™æ³•
    // msg: {
    //   handler(curVal, oldVal) {
    //     //æ”¹å˜å…¶ä»–å˜é‡çš„æ“ä½œ
    //   },
    //   immediately: true,
    //   deep: true,
    // },
  },
  computed: {
    // num() {
    //   //ç”¨å…¶ä»–å˜é‡è®¡ç®— returnå‡ºå€¼
    //   return "num";
    // },

    //é˜²æ­¢æ•°æ®æ›´æ–°ä¸åŠæ—¶ æ”¾computedèŽ·å–
    ...mapState({
      dataTest: (state) => state.dataTest,
    }),
  },
  filters: {
    //åƒåˆ†ä½
    NumFormat: function (value) {
      if (value != null || "") {
        return value.toLocaleString("en-US");
      }
    },
    //åƒåˆ†ä½å¹¶ä¿ç•™ä¸¤ä½å°æ•°
    NumFormat1: function (value) {
      if (value != null || "") {
        var intPartFormat = Number(value)
          .toFixed(2)
          .replace(/(\d)(?=(\d{3})+\.)/g, "$1,"); //Â å°†æ•´æ•°éƒ¨åˆ†é€¢ä¸‰ä¸€æ–­
        return intPartFormat;
      }
    },
  },
};
</script>

<style lang="less" scoped>
// @import "../main.less";

input::-webkit-input-placeholder {
  // è®¾ç½®è¾“å…¥æ¡†æ–‡å­—é¢œè‰²
}

div::-webkit-scrollbar,
span::-webkit-scrollbar {
  // è®¾ç½®æ»šåŠ¨æ¡æ ·å¼
  display: none;
}

/deep/.van-picker {
  width: 100vw;
}

span,
img {
  display: inline-block;
}

div,
span {
  -webkit-overflow-scrolling: touch;
}

.bj {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-size: 100% 100%;
}
</style>



