<template>
  <div class="bj">
    <div class="column">
      <span>proxy</span>
      <span class="data"> {{ result }}</span>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import dayjs from "dayjs";
// import { dateTransfor } from "../utils";
import { get, post } from "../http.js";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  components: {},
  data() {
    return {
      currentDate: null,
      result: null,
      Loading: null,
    };
  },
  created() {
    //å¼€æºapi get https://api.apiopen.top/getJoke?page=1&count=2&type=video

    this.$axios.get('https://api.apiopen.top/api/sentences')
        .then(function (response) {
            console.log(response);
        })
        .catch(function (error) {
            console.log(error);
       });

    // get('/getJoke',{page:1,count:3,type:'video'}).then(res => {
    //     console.log('enter get res', res)
    // }).catch(err => {

    // })
    this.loading = Toast.loading({
      duration: 0, // æŒç»­å±•ç¤º toast
      forbidClick: true,
      message: "åŠ è½½ä¸­...",
    });
    post("/getJoke", { page: 1, count: 2, type: "video" })
      .then((res) => {
        console.log("enter post res", res);
        this.result = res.result;
        this.loading.clear();
      })
      .catch((err) => {
        this.loading.clear();
      });
  },
  mounted() {        
  },
  methods: {
    ...mapMutations({
      changeDataTest: "changeDataTest",
    }),
  },
  watch: {
    //'list.xx':{} ç›‘å¬ä¸€ä¸ªå±žæ€§çš„å†™æ³•
    // show: {
    //   handler(curVal, oldVal) {
    //     //æ”¹å˜å…¶ä»–å˜é‡çš„æ“ä½œ
    //   },
    //   immediately: true,
    //   deep: true,
    // },
  },
  computed: {
    // num() {
    //   //ç”¨å…¶ä»–å˜é‡è®¡ç®— returnå‡ºå€¼
    //   return "num";
    // },

    //é˜²æ­¢æ•°æ®æ›´æ–°ä¸åŠæ—¶ æ”¾computedèŽ·å–
    ...mapState({
      dataTest: (state) => state.dataTest,
    }),
  },
  filters: {
    //åƒåˆ†ä½
    NumFormat: function (value) {
      if (value != null || "") {
        return value.toLocaleString("en-US");
      }
    },
    //åƒåˆ†ä½å¹¶ä¿ç•™ä¸¤ä½å°æ•°
    NumFormat1: function (value) {
      if (value != null || "") {
        var intPartFormat = Number(value)
          .toFixed(2)
          .replace(/(\d)(?=(\d{3})+\.)/g, "$1,"); //Â å°†æ•´æ•°éƒ¨åˆ†é€¢ä¸‰ä¸€æ–­
        return intPartFormat;
      }
    },
  },
};
</script>

<style lang="less" scoped>
// @import "../main.less";

input::-webkit-input-placeholder {
  // è®¾ç½®è¾“å…¥æ¡†æ–‡å­—é¢œè‰²
}

div::-webkit-scrollbar,
span::-webkit-scrollbar {
  // è®¾ç½®æ»šåŠ¨æ¡æ ·å¼
  display: none;
}

/deep/.van-picker {
  width: 100vw;
}

span,
img {
  display: inline-block;
}

div,
span {
  -webkit-overflow-scrolling: touch;
}

.bj {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-size: 100% 100%;
  .data {
    margin: 30px auto;
    width: 690px;
    height: 750px;
    overflow: auto;
    //   background: red;
  }
}
</style>



